.home {
    // width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .search-movie {
        input {
            margin-inline-end: 8px;
        }
        button.disabeld {
            cursor: not-allowed;
        }
    }
    .movie-list {
        margin-block-start: 15px;
        margin-block-end: 15px;
        width: 80vw;
        display: grid;
        gap: 12px;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
}
